import React from "react";
import f from "./Post.module.css";
const Post = (props) => {
  return (
    <div className={f.item}>
      <img src="data:image/jpeg;base64," />
        {props.massage}

      <div>
        <span>like:{props.lickesCaunt}</span>
      </div>
    </div>
  );
};
export default Post;
