
import React from "react";
import f from "../Navbar.module.css";


const FriendItem = (props) => {

    return (

   <div className={f.name}>
       <div className={f.img}>
       <img  src='data:image/jpeg;base64,'></img>
   </div>
       {props.name}
   </div>

    )
};

export default FriendItem;

